import React, { Component } from 'react'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import { createPost } from '../../api/post'
import { withRouter, Redirect } from 'react-router-dom'
import { PermMedia, Room, EmojiEmotions } from '@material-ui/icons'
import ImagesUpload from './imageUploader'

class CreatePost extends Component {
  constructor (props) {
    super(props)

    this.state = {
      description: '',
      img: [],
      likes: 0
    }
  }

	handleChange = (event) =>
	  this.setState({
	    [event.target.name]: event.target.value
	    // img: event.target.img
	  })

	handleUpload = (e) => {
	  // return;

	  const images = e

	  const imagesArray = []
	  images.map((image) => imagesArray.push(image))

	  imagesArray.length > 0
	    ? this.setState({ img: imagesArray })
	    : this.setState({ img: [] })
	}

	handleSubmit = (event) => {
	  event.preventDefault()
	  const { user, msgAlert, history, newAddPost } = this.props

	  const formData = new FormData()
	  const images = this.state.img

	  images.map((img) => formData.append('photo', img))
	  formData.append('description', this.state.description)
	  formData.append('likes', this.state.likes)

	  //   createPost(this.state, user, formData)
	  createPost(formData, user)
	    .then((res) => {
	      console.log(res)
	      newAddPost({
	        _id: res.data.post._id,
	        description: res.data.post.description,
	        img: res.data.post.img
	      })
	    })
	    .then(() => {
	      this.setState({ description: '', img: [] })
	    })
	    .then(() => history.push('/home'))
	    .then(() => {
	      msgAlert({
	        heading: 'New Post created',
	        message: 'Created successfully!',
	        variant: 'success'
	      })
	    })
	    .catch((error) => {
	      msgAlert({
	        heading: 'Failed to create new post',
	        message: 'Post error: ' + error.message,
	        variant: 'danger'
	      })
	    })
	  if (this.state.description) {
	    return <Redirect to={'/posts'} />
	  }
	}

	render () {
	  return (
	    <Form onSubmit={this.handleSubmit} className='form' method='POST'>
	      <Form.Group controlId='description'>
	        <div className='shareInput'>
	          <img
	            src={process.env.PUBLIC_URL + '/images/blank-profile-picture'}
	            className='shareProfileImg'
	            alt='profile-pic'
	          />
	          <Form.Control
	            required
	            name='description'
	            value={this.state.description}
	            placeholder="What's on your mind today?"
	            onChange={this.handleChange}
	          />
	        </div>
	      </Form.Group>
	      <hr className='shareHr' />
	      <div className='shareBottom'>
	        <label htmlFor='file' className='shareOptions'>
	          <PermMedia className='shareIcon' />
	          <ImagesUpload name='file' multiple onChange={this.handleUpload} />

	          <Room className='shareIcon' />
	          <span>Tag Location</span>
	          <EmojiEmotions className='shareIcon' />
	          <span>Feelings</span>
	        </label>
	        <div>
	          <Button
	            style={{ backgroundColor: '#ba6b6c', border: 'none' }}
	            type='submit'
	            className='shareButton'>
							Share
	          </Button>
	        </div>
	      </div>
	    </Form>
	  )
	}
}

export default withRouter(CreatePost)
